___CSS_LOADER_EXPORT___.push([module.id, `.start-entry {
  width: auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.start-entry .entry-inputs {
  width: auto;
  height: fit-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.start-entry .entry-inputs .entry-input {
  width: 220px;
  border: none;
  border-bottom: 1px solid #cdcdcd;
  background-color: transparent;
  color: inherit;
  outline: none;
  font-size: 18px;
  color: #cdcdcd;
  opacity: 0.8;
}

.start-entry .entry-inputs .entry-input::placeholder {
  font-size: 18px;
  color: #cdcdcd;
  opacity: 0.7;
  text-transform: capitalize;
}

.start-entry .entry-inputs .valid-err {
  animation: error 2s ease;
}

@keyframes error {
  0% {
    border-bottom: 1px solid #cdcdcd;
  }
  50% {
    border-bottom: 1px solid #ff0000;
  }
  100% {
    border-bottom: 1px solid #cdcdcd;
  }
}
.start-entry .entry-inputs .entry-input::placeholder {
  font-size: 18px;
  color: #cdcdcd;
  opacity: 0.7;
  visibility: hidden;
}

.start-entry .entry-inputs .inputs-label {
  font-size: 18px;
  font-weight: 100;
  font-family: "Montserrat", sans-serif;
  color: #cdcdcd;
  text-transform: capitalize;
  position: relative;
  bottom: 30%;
  opacity: 0.7;
  transition: all 0.2s ease;
  pointer-events: none;
}

.start-entry .entry-inputs .entry-input:focus + .inputs-label {
  transform: translateY(-100%); /* Move label up */
}

.start-entry .entry-inputs .entry-input:not(:placeholder-shown) + .inputs-label {
  transform: translateY(-100%); /* Move label up */
}

.start-entry .entry-inputs .valid-err__lable {
  animation: errorLable 2s ease;
}

@keyframes errorLable {
  0% {
    color: #cdcdcd;
  }
  50% {
    color: #ff0000;
  }
  100% {
    color: #cdcdcd;
  }
}
.start-entry .entry-btn {
  width: fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.start-entry .entry-btn .btn-log {
  width: 100px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #570071;
  cursor: pointer;
  opacity: 0.8;
  transition: opacity 0.5s ease;
}

.start-entry .entry-btn .btn-log .log-title {
  font-size: 18px;
  font-weight: 200;
  letter-spacing: 1px;
  color: #ececec;
  text-transform: capitalize;
}

.start-entry .entry-btn .btn-log:hover {
  opacity: 1;
}

.entry-close--open {
  display: none;
}

.entry-hide-anim {
  animation: hideEntry 2s ease;
}

@keyframes hideEntry {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}
.entry-open-anim {
  animation: openEntry 2s ease;
}

@keyframes openEntry {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
